@use 'colors' as *;

/* Borders */

$border-accent-size: 8px;
$border-depth-size: 1px;
$border-size: 2px;

/* Border radius */

$border-radius-small: 2px;
$border-radius-regular: 4px;
$border-radius-large: 8px;
$border-radius-logo: 16px;
$border-radius-round: 32px;

/* Sizes */

$tab-highlight-height: 4px;

$button-border-radius: $border-radius-regular;

/* Mixins */

@mixin bottom-border-shadow {
  box-shadow: inset 0 -#{$border-size} $grey-070;
}

@mixin bottom-border-shadow-dark {
  box-shadow: inset 0 -#{$border-size} $grey-090;
}
